/* App.css */
@font-face {
  font-family: 'Friendly Fire';
  src: local('Friendly Fire'), url(../public/fonts/Friendly_Fire.ttf);
}

@font-face {
  font-family: 'Kenyan Coffee';
  src: local('Kenyan Coffee'), url(../public/fonts/kenyan_coffee_rg.otf);
}

body {
  justify-content: center;
  font-family: 'Kenyan Coffee';
  background-color: #2a1552;
  font-family: Arial, sans-serif;
  color: white;
  margin: 0;
  padding: 0;
  display: flex;
  height: 100vh;
}

h2 {
  padding: 10px;
  font-family: 'Friendly Fire';
  font-size: 2.7em;
  letter-spacing: 0.1em;
  margin: auto;
  max-width: 90rem;
  text-shadow: -0.1em -0.1em 0 #000, 0.1em -0.1em 0 #000, -0.1em 0.1em 0 #000,
    0.1em 0.1em 0 #000, 0 -0.1em 0 #000, 0.1em 0 0 #000, 0 0.1em 0 #000,
    -0.1em 0 0 #000;
  text-transform: uppercase;
}

h3 {
  padding: 15px;
  font-family: 'Friendly Fire';
  font-size: 2.25em;
  letter-spacing: 0.1em;
  margin: auto;
  max-width: 90rem;
  text-shadow: -0.1em -0.1em 0 #000, 0.1em -0.1em 0 #000, -0.1em 0.1em 0 #000,
    0.1em 0.1em 0 #000, 0 -0.1em 0 #000, 0.1em 0 0 #000, 0 0.1em 0 #000,
    -0.1em 0 0 #000;
  text-transform: uppercase;
}

.page {
  align-items: center;
  justify-content: left;
}

p {
  font-family: 'Friendly Fire';
  padding-left: 25px;
  font-size: 1.8em;
  letter-spacing: 0.1em;
  margin: 10px;
  max-width: 90rem;
  text-shadow: -0.1em -0.1em 0 #000, 0.1em -0.1em 0 #000, -0.1em 0.1em 0 #000,
    0.1em 0.1em 0 #000, 0 -0.1em 0 #000, 0.1em 0 0 #000, 0 0.1em 0 #000,
    -0.1em 0 0 #000;
  text-transform: uppercase;
}

button {
  background: none;
  color: inherit;
  border: none;
  font-family: 'Friendly Fire';
  font-size: 2rem;
  text-transform: uppercase;
  text-shadow: -0.1em -0.1em 0 #000, 0.1em -0.1em 0 #000, -0.1em 0.1em 0 #000,
    0.1em 0.1em 0 #000, 0em -0.1em 0 #000, 0.1em 0em 0 #000, 0em 0.1em 0 #000,
    -0.1em 0em 0 #000;
  letter-spacing: 0.1em;
  background-color: rgb(242, 175, 28);
  border: solid rgb(200, 144, 21) 0;
  border-bottom: solid rgb(200, 144, 21) 0.2em;
  outline: solid black 0.1em;
  border-radius: 0.75rem;
  padding: 0.5rem 1rem;
  user-select: none;
  transition: border-color 0.1s, background-color 0.1s, color 0.1s, outline 0.1s;
}

button:hover {
  border-color: transparent;
  outline: solid white 0.1em;
}

button:active {
  border-color: transparent;
  background-color: white;
}

input[type='text'] {
  background: none;
  background-color: #160d2e;
  border: 0.25rem solid #f0eeda;
  border-radius: 0.75rem;
  color: inherit;
  font-family: 'Friendly Fire';
  font-size: 2rem;
  letter-spacing: 0.1em;
  padding: 0.5rem;
}

.small-card-image {
  width: 100px;
  height: auto;
  margin: 5px;
}

.deckStatistics > p {
  font-size: 2rem
}

.menu {
  display: flex;
  justify-content: center;
  gap: 1rem;
  align-items: center;
  padding: 0.5rem 1rem;
  margin-top: 18em;
  border: solid rgb(68, 51, 154) 0;
  border-bottom: solid rgb(68, 51, 154) 0.2em;
  outline: solid black 0.2em;
  border-radius: 0.5rem;
  background-color: rgb(84, 64, 187);
}

.menu > * {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Style for the card container */
.card {
  gap: 10em;
  display: inline-flex;
  width: 150px;
  margin: 6px;
  text-align: center;
}

.plus {
  font-size: 2em;
}

.combo {
  display: flex;
  align-items: center;
  padding: 5px;
}

/* Style for card content */
.card-content {
  position: relative; /* Set the position to relative */
}

/* Style for card image */
.card-image {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

/* Style for cost icon */
.cost-icon {
  position: absolute; /* Set the position to absolute */
  top: 1px; /* Adjust the top position to place it at the top */
  right: 1px; /* Adjust the right position to place it at the top-right corner */
  max-width: 25px;
  height: auto;
  display: block;
}

.level-icon {
  position: absolute; /* Set the position to absolute */
  top: 3px; /* Adjust the top position to place it at the top */
  right: 63px; /* Adjust the right position to place it at the top-right corner */
  max-width: 22px;
  height: auto;
  display: block;
}

.deck-images,
.characterCard {
  padding: 15px;
}

/* Define the CSS classes for different cost colors */
.green-cost {
  color: green;
}

.yellow-cost {
  color: yellow;
}

.red-cost {
  color: red;
}

.blue-cost {
  color: blue;
}

/* Style for the plus icon */
.plus {
  font-size: 2rem; /* Increase the font size of the plus icon */
}
